import React, { useState } from 'react';
import { Check } from 'lucide-react';
import { COLORS } from '../theme/colors.js';

// fills the modal height; content scrolls; footer floats centered at the BOTTOM
function StepLayout({ children, footer }) {
  return (
    <div
      className="
        relative h-full min-h-[560px] max-h-[85vh]
        flex flex-col
      "
      style={{ height: '100%' }} // hard fallback in case parent doesn't give h-full
    >
      {/* scrollable content; leave space for footer */}
      <div className="flex-1 overflow-y-auto px-6 pt-16 pb-[160px]">
        {children}
      </div>

      {/* floating footer pinned to bottom of the modal */}
      <div
        className="
          absolute inset-x-0 bottom-0 z-10
          px-4 pt-6 pb-6
          bg-gradient-to-t from-white via-white/95 to-white/60
        "
      >
        <div className="mx-auto w-full max-w-[520px]">
          {footer}
        </div>
      </div>
    </div>
  );
}

export default function EmailAuth({ onSuccess, testMode = true }) {
  const [email, setEmail] = useState('');
  const [code, setCode] = useState('');
  const [step, setStep] = useState('email'); // 'email' | 'code' | 'success'
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState('');

  const validateEmail = (v) => /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(v);

  const handleEmailSubmit = async (e) => {
    e.preventDefault();
    setError('');
    if (!validateEmail(email)) {
      setError('Please enter a valid email address');
      return;
    }
    setIsLoading(true);
    try {
      if (testMode) {
        setTimeout(() => {
          setStep('code');
          setIsLoading(false);
        }, 600);
      } else {
        const apiKey = window.onairosApiKey || 'test-key';
        const res = await fetch('https://api2.onairos.uk/email/verification', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json', Authorization: `Bearer ${apiKey}` },
          body: JSON.stringify({ email, action: 'request' })
        });
        if (!res.ok) throw new Error('Failed to send verification code');
        const data = await res.json();
        if (!data.success) throw new Error(data.error || 'Failed to send verification code');
        setStep('code');
        setIsLoading(false);
      }
    } catch (err) {
      setError(err.message || 'Something went wrong');
      setIsLoading(false);
    }
  };

  const handleGoogleAuth = async () => {
    try {
      const baseUrl = 'https://api2.onairos.uk';
      const username =
        localStorage.getItem('username') ||
        localStorage.getItem('onairosUser')?.email ||
        'user@example.com';

      const params = new URLSearchParams({
        username,
        sdk_type: 'web',
        return_url: window.location.origin + '/oauth-callback.html'
      });
      const fullUrl = `${baseUrl}/gmail/authorize?${params.toString()}`;

      const popup = window.open(
        fullUrl,
        'google_oauth',
        'width=500,height=600,scrollbars=yes,resizable=yes'
      );
      if (!popup) throw new Error('Popup blocked. Please allow popups for this site.');
      const iv = setInterval(() => {
        if (popup.closed) {
          clearInterval(iv);
          onSuccess({
            email: 'user@gmail.com',
            method: 'google',
            connectedAccounts: { Google: true }
          });
        }
      }, 800);
    } catch (err) {
      setError('Google authentication failed. Please try again.');
    }
  };

  const handleCodeSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setIsLoading(true);
    try {
      if (testMode) {
        if (code === '123456' || code.length === 6) {
          setStep('success');
          setTimeout(() => {
            onSuccess({
              email,
              verified: true,
              token: 'test-token-' + Date.now(),
              userName: email.split('@')[0],
              existingUser: false,
              accountInfo: null,
              isNewUser: true,
              flowType: 'onboarding',
              adminMode: false,
              userCreated: true,
              accountDetails: {
                email,
                createdAt: new Date().toISOString(),
                testAccount: true
              }
            });
          }, 500);
        } else {
          setError('Invalid code. Use any 6-digit code (e.g., 123456) for testing.');
          setIsLoading(false);
        }
      } else {
        const apiKey = window.onairosApiKey || 'test-key';
        const res = await fetch('https://api2.onairos.uk/email/verification', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json', Authorization: `Bearer ${apiKey}` },
          body: JSON.stringify({ email, action: 'verify', code })
        });
        if (!res.ok) throw new Error('Invalid verification code');
        const data = await res.json();
        if (!data.success) throw new Error(data.error || 'Verification failed');
        setStep('success');
        setTimeout(() => {
          onSuccess({
            email,
            verified: true,
            token: data.token || data.jwtToken,
            userName: data.userName,
            existingUser: data.existingUser,
            accountInfo: data.accountInfo,
            isNewUser: !data.existingUser,
            flowType: data.existingUser ? 'dataRequest' : 'onboarding',
            adminMode: data.adminMode,
            userCreated: data.userCreated,
            accountDetails: data.accountDetails
          });
        }, 800);
      }
    } catch (err) {
      setError(err.message || 'Invalid code. Please try again.');
      setIsLoading(false);
    }
  };

  const FooterButton = ({ onClick, disabled, loading, children }) => (
    <button
      type="button"
      onClick={onClick}
      disabled={disabled}
      className="
        w-full h-14
        bg-gray-900 hover:bg-gray-800 text-white
        rounded-full text-base font-medium
        flex items-center justify-center gap-2
        transition-colors disabled:opacity-50
        shadow-lg
      "
      style={{ fontFamily: 'Inter, system-ui, sans-serif' }}
    >
      {loading ? 'Loading...' : children}
      <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24" aria-hidden="true">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
      </svg>
    </button>
  );

  const renderEmailStep = () => (
    <StepLayout
      footer={
        <>
          <button
            type="button"
            className="
              w-full h-12 bg-white
              text-gray-900 rounded-xl
              border border-gray-200 hover:bg-gray-50
              flex items-center justify-center gap-3 transition-colors
              mb-3
            "
            style={{ fontFamily: 'Inter, system-ui, sans-serif' }}
            onClick={handleGoogleAuth}
          >
            <svg className="w-5 h-5" viewBox="0 0 24 24" aria-hidden="true">
              <path fill="#4285F4" d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z" />
              <path fill="#34A853" d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z" />
              <path fill="#FBBC05" d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z" />
              <path fill="#EA4335" d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z" />
            </svg>
            Continue with Google
          </button>

          <FooterButton
            onClick={handleEmailSubmit}
            disabled={isLoading || !email.trim()}
            loading={isLoading}
          >
            Continue
          </FooterButton>
        </>
      }
    >
      <div className="text-center">
        <div className="mb-8">
          <h1 className="text-2xl font-bold text-gray-900 mb-2 leading-tight">Your AI persona is getting closer</h1>
          <p className="text-gray-600 text-base">Sign in or create an account</p>
        </div>
        <div className="mb-6">
          <input
            type="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            placeholder="Enter your email"
            className="
              w-full px-4 py-4 text-base
              bg-gray-50 border-0 rounded-xl
              placeholder:text-gray-400
              focus:bg-white focus:ring-2 focus:ring-gray-200 outline-none
              transition-all duration-200
            "
            style={{ fontFamily: 'Inter, system-ui, sans-serif' }}
            required
          />
        </div>
        <div className="mb-6">
          <span className="inline-flex items-center gap-3 text-gray-500 text-sm">
            <span className="block w-24 h-px bg-gray-200" />
            Or
            <span className="block w-24 h-px bg-gray-200" />
          </span>
        </div>
        {error && <p className="text-sm" style={{ color: COLORS.error }}>{error}</p>}
      </div>
    </StepLayout>
  );

  const renderCodeStep = () => (
    <StepLayout
      footer={
        <FooterButton
          onClick={handleCodeSubmit}
          disabled={isLoading || code.length !== 6}
          loading={isLoading}
        >
          Continue
        </FooterButton>
      }
    >
      <div className="w-full mb-10">
        <h1
          className="font-bold text-left mb-2"
          style={{
            fontFamily: 'IBM Plex Sans, system-ui, sans-serif',
            fontWeight: '700',
            fontSize: '24px',
            lineHeight: '32px',
            color: COLORS.textPrimary
          }}
        >
          Enter verification code
        </h1>
        <p
          className="text-left mb-2"
          style={{
            fontFamily: 'Inter, system-ui, sans-serif',
            fontWeight: '400',
            fontSize: '16px',
            lineHeight: '24px',
            color: COLORS.textSecondary
          }}
        >
          We've sent a 6-digit code to {email}
        </p>
      </div>

      {testMode && (
        <div
          className="p-3 rounded-lg border text-center mb-6"
          style={{ backgroundColor: '#EBF8FF', borderColor: '#BEE3F8', color: '#2B6CB0' }}
        >
          <p className="text-sm">Test mode: Use code 123456</p>
        </div>
      )}

      <div className="px-4 mb-6">
        <div className="flex justify-center space-x-3">
          {Array.from({ length: 6 }, (_, i) => (
            <input
              key={i}
              type="text"
              maxLength="1"
              value={code[i] || ''}
              onChange={(e) => {
                const arr = code.split('');
                arr[i] = e.target.value;
                setCode(arr.join(''));
                if (e.target.value && i < 5) {
                  const next = e.target.parentElement?.children[i + 1];
                  next && next.focus();
                }
              }}
              onKeyDown={(e) => {
                if (e.key === 'Backspace' && !code[i] && i > 0) {
                  const prev = e.target.parentElement?.children[i - 1];
                  prev && prev.focus();
                }
              }}
              className="w-12 h-12 border rounded-lg text-center text-lg font-medium focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none"
              style={{ borderColor: COLORS.border, backgroundColor: COLORS.background, fontFamily: 'Inter, system-ui, sans-serif' }}
            />
          ))}
        </div>
      </div>

      {error && (
        <div className="mb-6">
          <p className="text-sm text-center" style={{ color: COLORS.error }}>{error}</p>
        </div>
      )}
    </StepLayout>
  );

  const renderSuccessStep = () => (
    <StepLayout footer={<div />}>
      <div className="w-full flex flex-col items-center space-y-6">
        <div className="flex items-center justify-center w-16 h-16 rounded-full" style={{ backgroundColor: '#D1FAE5' }}>
          <Check className="w-8 h-8" style={{ color: COLORS.success }} />
        </div>
        <div className="text-center space-y-2">
          <h2 className="text-xl font-semibold" style={{ color: COLORS.textPrimary }}>
            Email verified!
          </h2>
          <p style={{ color: COLORS.textSecondary }}>Setting up your account...</p>
        </div>
      </div>
    </StepLayout>
  );

  return (
    <div className="w-full h-full" style={{ height: '100%' }}>
      {step === 'email' && renderEmailStep()}
      {step === 'code' && renderCodeStep()}
      {step === 'success' && renderSuccessStep()}
    </div>
  );
}
